/* Footer */
footer {
    text-align: center;
    padding: 15px 0;
    background-color: #262626;
    border-top: 5px solid $mainColor;
    p {
        margin: 0;
        color: #fff
    }
    .scroll-top {
        position: fixed;
        bottom: 15px;
        right: 15px;
        width: 40px;
        height: 40px;
        line-height: 40px;
        background-color: $mainColor;
        border-radius: 3px;
        border: 1px solid #ccc;
        color: #fff;
        transition: .2s;
        cursor: pointer;
        display: none;
        z-index: 99999;
        &:hover {
            padding-bottom: 50px;
        }
    }
}