/* Section About Us */
.about-us {
    padding: 60px 30px;
    .info {
        background-color: #f8f8f8;
        padding: 20px;
        border-radius: 3px;
        margin: 20px 0;
        box-shadow: 0 0 6px 0 rgba(#262626, .3);
        border: 1px solid #ccc;
        transition: .2s;
        &:hover {
            box-shadow: 0 0 3px 0 rgba(#262626, .1);
        }
        h2 {
            font-family: 'Harmattan', sans-serif;
            font-size: 25px;
            font-weight: bold;
            background-color: $brandColor;
            width: 20%;
            border-right: 5px solid $mainColor;
            padding: 5px 10px;
            color: #fff;
            @media #{$mediaExSmall} {
                width: 70%;
                font-size: 20px
            }
            @media #{$mediaSmall} {
                width: 60%;
                font-size: 21px
            }
            @media #{$mediaMedium} {
                width: 60%;
                font-size: 22px
            }
            @media #{$mediaLarge} {
                width: 60%;
                font-size: 24px
            }
        }
        p {
            font-size: 18px;
            font-weight: 500;
            @media #{$mediaExSmall} {
                font-size: 15px
            }
            @media #{$mediaSmall} {
                font-size: 15px
            }
            @media #{$mediaMedium} {
                font-size: 16px
            }
            @media #{$mediaLarge} {
                font-size: 17px
            }
        }
    }
}

/* Section discusions */
.discusions {
    padding: 60px 30px;
    background-color: #f8f8f8;
    .content {
        position: relative;
        background-color: #fff;
        padding: 20px;
        border-radius: 3px;
        box-shadow: 0 0 6px 0 #00000040;
        height: 220px;
        border: 1px solid #ccc;
        transition: .2s;
        &:hover {
            box-shadow:  0 0 3px 0 #00000010;
        }
        @media #{$mediaExSmall} {
            margin-bottom: 60px;
            height: 250px;
        }
        @media #{$mediaSmall} {
            margin-bottom: 60px
        }
        @media #{$mediaMedium} {
            margin-bottom: 40px
        }
        .icon {
            position: absolute;
            left: 10px;
            bottom: -25px;
            background-color: $brandColor;
            width: 60px;
            height: 60px;
            padding: 10px;
            border-radius: 50%;
            border: 1px solid $mainColor
        }
        p {
            font-size: 15px;
            font-weight: 500;
            line-height: 25px;
            @media #{$mediaExSmall} {
                font-size: 13px;
            }
            @media #{$mediaSmall} {
                font-size: 13px;
            }
            @media #{$mediaMedium} {
                font-size: 14px;
            }
            @media #{$mediaLarge} {
                font-size: 15px
            }
        }
    }
}


/* Section Cars */
.cars {
    padding: 100px 30px;
    background-image: url('../images/4 - Copy.jpg');
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($mainColor, .4)
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#262626, .5)
    }
    .text {
        position: relative;
        z-index: 99;
        color: #fff;
        h2 {
            font-family: 'Harmattan', sans-serif;
            width: 25%;
            background-color: $brandColor;
            font-size: 30px;
            font-weight: 700;
            color: #fff;
            text-shadow: 0 0 3px #00000050;
            padding: 10px;
            box-shadow: 0 0 3px 0 #00000040;
            border-bottom: 1px solid transparent;
            margin: 0;
            @media #{$mediaExSmall} {
                width: 60%;
                font-size: 20px;
            }
            @media #{$mediaSmall} {
                width: 50%;
                font-size: 21px;
            }
            @media #{$mediaMedium} {
                width: 40%;
                font-size: 25px;
            }
            @media #{$mediaLarge} {
                font-size: 28px
            }
        }
        p {
            width: 80%;
            font-size: 18px;
            padding: 10px;
            font-weight: 500;
            background: #fff;
            color: #000;
            margin: 0;
            @media #{$mediaExSmall} {
                width: 100%;
                font-size: 15px;
            }
            @media #{$mediaSmall} {
                width: 90%;
                font-size: 15px;
            }
            @media #{$mediaMedium} {
                width: 85%;
                font-size: 16px;
            }
            @media #{$mediaLarge} {
                width: 80%;
                font-size: 16px;
            }
        }
    }
    .img {
        position: relative;
        z-index: 99;
        text-align: center;
        margin-top: 40px;
        width: 70%;
        margin: 30px auto;
        @media #{$mediaExSmall} {
            width: 100%;
        }
        @media #{$mediaSmall} {
            width: 100%;
        }
    }
}

/* Section Repair */
.repair {
    padding: 60px 30px;
    .text {
        h2 {
            font-family: 'Harmattan', sans-serif;
            font-size: 25px;
            font-weight: 600;
            color: $mainColor;
            &:nth-child(2) {
                color: $brandColor
            }
            @media #{$mediaExSmall} {
                font-size: 20px;
            }
            @media #{$mediaSmall} {
                font-size: 21px;
            }
            @media #{$mediaMedium} {
                font-size: 23px;
            }
            @media #{$mediaLarge} {
                font-size: 23px;
            }
        }
    }
    .info {
        margin-top: 50px;
        h3 {
            font-family: 'Harmattan', sans-serif;
            font-size: 35px;
            color: $mainColor;
            font-weight: bold;
            text-align: center;
            @media #{$mediaExSmall} {
                font-size: 30px;
            }
            @media #{$mediaSmall} {
                font-size: 31px;
            }
            @media #{$mediaMedium} {
                font-size: 33px;
            }
            @media #{$mediaLarge} {
                font-size: 33px;
            }
        }
        .cont {
            position: relative;
            padding: 20px;
            margin-top: 30px;
            border-radius: 3px;
            box-shadow: 0 0 6px 0 #00000040;
            height: 220px;
            background-color: #f8f8f8;
            border: 1px solid #ccc;
            transition: .2s;
            &:hover {
                box-shadow:  0 0 3px 0 #00000010;
            }
            .num {
                position: absolute;
                right: 10px;
                top: -25px;
                background-color: $brandColor;
                width: 40px;
                height: 40px;
                line-height: 40px;
                color: #fff;
                font-size: 20px;
                font-weight: bold;
                text-align: center;
                border-radius: 50%;
                border: 1px solid #ccc;
            }
            p {
                font-size: 15px;
                font-weight: 500;
                line-height: 23px;
                @media #{$mediaExSmall} {
                    font-size: 13px;
                }
                @media #{$mediaSmall} {
                    font-size: 13px;
                }
                @media #{$mediaMedium} {
                    font-size: 13px;
                }
                @media #{$mediaLarge} {
                    font-size: 13px;
                }
                span {
                    font-weight: bold
                }
            }
        }
    }
}

/* Section Owner */
.owner {
    padding: 60px 30px;
    background-color: #f8f8f8;
    .text {
        h2 {
            font-family: 'Harmattan', sans-serif;
            font-size: 28px;
            font-weight: 600;
            color: $mainColor;
            @media #{$mediaExSmall} {
                font-size: 22px;
            }
            @media #{$mediaSmall} {
                font-size: 23px;
            }
            @media #{$mediaMedium} {
                font-size: 25px;
            }
            @media #{$mediaLarge} {
                font-size: 26px;
            }
        }
    }
    p {
        font-size: 16px;
        background-color: #fff;
        padding: 20px;
        margin: 30px 0 80px;
        border-radius: 3px;
        box-shadow: 0 0 6px 0 #00000040;
        border: 1px solid #ccc;
        transition: .2s;
        &:hover {
            box-shadow:  0 0 3px 0 #00000010;
        }
        @media #{$mediaExSmall} {
            font-size: 14px;
            margin: 15px 0;
        }
        @media #{$mediaSmall} {
            font-size: 14px;
            margin: 15px 0;
        }
        @media #{$mediaMedium} {
            font-size: 15px;
        }
        @media #{$mediaLarge} {
            font-size: 15px;
        }
    }
    .img {
        width: 300px;
        height: 230px;
        margin: auto;
        background-color: #fff;
        border: 10px solid #fff;
        border-bottom-width: 20px;
        margin-bottom: 40px;
        border-radius: 3px;
        overflow: hidden;
        position: relative;
        box-shadow: 0 0 3px 0 #00000040;
        @media #{$mediaExSmall} {
            width: auto;
        }
        @media #{$mediaSmall} {
            width: auto;
        }
        @media #{$mediaMedium} {
            width: auto;
        }
        @media #{$mediaLarge} {
            width: auto;
        }
        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(#262626, .7);
            transition: .3s
        }
        img { transition: .3s }
        &:hover {
            img {
                transform: scale(1.1)
            }
            &::after {
                background-color: rgba(#262626, .4)
            } 
        }
    }
}

/* Contact Us */
.contact-us {
    padding: 60px 30px;
    .text {
        width: 80%;
        text-align: center;
        margin: auto;
        background-color: #f8f8f8;
        box-shadow: 0 0 6px 0 #00000040;
        border-radius: 3px;
        border: 1px solid #ccc;
        padding: 30px;
        transition: .2s;
        &:hover {
            box-shadow:  0 0 3px 0 #00000010;
        }
        @media #{$mediaExSmall} {
            width: 100%;
        }
        @media #{$mediaSmall} {
            width: 100%;
        }
        @media #{$mediaMedium} {
            width: 100%;
        }
        @media #{$mediaLarge} {
            width: 100%;
        }
        h2 {
            font-family: 'Harmattan', sans-serif;
            font-size: 35px;
            font-weight: bold;
            color: $mainColor;
            @media #{$mediaExSmall} {
                font-size: 28px;
            }
            @media #{$mediaSmall} {
                font-size: 28px;
            }
            @media #{$mediaMedium} {
                font-size: 30px;
            }
            @media #{$mediaLarge} {
                font-size: 32px;
            }
        }
        p {
            font-size: 18px;
            font-weight: 500;
            margin: 15px 0;
            @media #{$mediaExSmall} {
                font-size: 15px;
            }
            @media #{$mediaSmall} {
                font-size: 15px;
            }
            @media #{$mediaMedium} {
                font-size: 16px;
            }
            @media #{$mediaLarge} {
                font-size: 17px;
            }
        }
    }
    .scoail {
        text-align: center;
        .btn {
            @media #{$mediaExSmall} {
                padding: 5px;
            }
            @media #{$mediaSmall} {
                padding: 5px;
            }
            
            &:focus {
                outline: 0;
                box-shadow: none
            }
            i {
                font-size: 18px;
                width: 40px;
                height: 40px;
                line-height: 40px;
                border-radius: 3px;
                text-align: center;
                color: #fff;
                background-color: #0e1016;
                transition: .3s ease;
                @media #{$mediaExSmall} {
                    font-size: 15px;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                }
                @media #{$mediaSmall} {
                    font-size: 15px;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                }
                @media #{$mediaMedium} {
                    font-size: 16px;
                    width: 35px;
                    height: 35px;
                    line-height: 35px;
                }
                @media #{$mediaLarge} {
                    font-size: 17px;
                }
            }
            &:nth-child(1):hover {
                i {
                    background-color: #3b5999
                }
            }
            &:nth-child(2):hover {
                i {
                    background-color: #55acee
                }
            }
            &:nth-child(3):hover {
                i {
                    background-color: #e4405f
                }
            }
            &:nth-child(4):hover {
                i {
                    background-color: #0077B5
                }
            }
        }
    }
}