/* Header */
header {
    width: 100%;
    height: 100vh;
    background-image: url("../images/slide1-1.jpg");
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($mainColor, .3)
    }
    .text {
        text-align: right;
        h1 {
            position: relative;
            z-index: 999;
            padding: 30px;
            color: #fff;
            border-radius: 3px;
            font-size: 28px;
            font-weight: 800;
            width: 60%;
            line-height: 45px;
            @media #{$mediaExSmall} {
                width: 90%;
                font-size: 20px
            }
            @media #{$mediaSmall} {
                width: 80%;
                font-size: 20px
            }
            @media #{$mediaMedium} {
                width: 70%;
                font-size: 23px
            }
            @media #{$mediaLarge} {
                width: 60%;
                font-size: 26px
            }
            span {
                padding: 5px 10px;
                background-color: rgba($brandColor, 1);
            }
        }
    }
}