/* Navigation Bar */
.navbar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: transparent;
    height: 70px;
    z-index: 9999;
    .navbar-brand {
        height: 70px;
    }
    .navbar-collapse {
        position: relative;
        z-index: 99999;
        @media #{$mediaExSmall} {
            background-color: rgba($mainColor, .8);
        }
        @media #{$mediaSmall} {
            background-color: rgba($mainColor, .8);
        }
        @media #{$mediaMedium} {
            background-color: rgba($mainColor, .8);
        }
        .navbar-nav {
            .nav-link {
                padding: 10px;
                color: #fff;
                font-weight: bold;
                border-radius: 3px;
                margin: 10px;
                transition: .2s;
                &:hover {
                    color: $brandColor
                }
            }
        }
    }
}